<template>
  <div class="if_test">
    <div class="head">
      <img src="../assets/logo.png" class="tps_logo"/>
      <div style="float: left;">
        <p class="tps_title">TPS</p>
      </div>
      <div class="btn-group quit">
        <button type="button" class="btn btn-primary" style="background: none;">注销</button>
        <button type="button" class="btn btn-primary dropdown-toggle" data-toggle="dropdown">
          <span class="caret"></span>
          <span class="sr-only">切换下拉菜单</span>
        </button>
        <ul class="dropdown-menu" role="menu">
          <li><a href="#">登录</a></li>
        </ul>
      </div>
    </div>
    <form class="bs-example bs-example-form" role="form">
      <div class="row">
        <div class="search">
          <div class="input-group">
            <input type="text" class="form-control" v-model="bondIdStr"/>
            <span class="input-group-btn">
                        <button class="btn btn-default" type="button" @click="search()">Search</button>
                    </span>
          </div><!-- /input-group -->
        </div><!-- /.col-lg-6 -->
      </div><!-- /.row -->
    </form>
    <table class="table table-hover">
      <thead>
      <tr>
        <th>ID</th>
        <th>Price</th>
        <th>MaturityDate</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="bd in bondsList">
        <td>{{bd.bondId}}</td>
        <td>{{bd.firstPrice}}</td>
        <td>{{bd.maturityDate}}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import { setCookie,getCookie,delCookie } from '../assets/js/cookie.js'
  export default {
    name: 'test',
    props: ['myMessage'],
    data() {
      return {
        id: this.myMessage,
        bondIdStr: '',
        bondsList: null
      }
    },
    mounted() {
      /*页面挂载获取保存的cookie值，渲染到页面上*/
      /*  let uname = getCookie('id')
      this.id = uname

      if(uname == ""){
        this.$router.push('/')
      }*/
      this.$http({

        method: "get",
        url: "../static/home.json",
        dataType: "json",
        crossDomain: true,

        cache: false

      }).then(resolve => {
        this.bondsList = resolve.data.users;
      }),

        reject => {

          console.log("请求失败", reject);

        };
    },
    methods: {
      search() {
        let searchData = {
          "ID": this.id,
          "bondIdStr": this.bondIdStr,
          "currentView": 'Test'
        };
        this.$http({

          method: "get",
          url: "../static/home.json",
          data: searchData,
          dataType: "json",
          crossDomain: true,
          cache: false

        }).then(resolve => {
          this.bondsList = resolve.data.users;
        }),

          reject => {

            console.log("请求失败", reject);

          };
   /*     $.ajax({
          type: "post",
          url: "http://localhost:8080/TPS_Team/first.do",
          data: searchData,
          dataType: json,
          success: function (data) {
            this.boundsList = JSON.stringify(data)
          },
          error: function (jqObj) {
            console.log(jqObj.status)
          }
        });*/
      },
      quit() {
        /*删除cookie*/
        delCookie('id')
      }
    }
  }

</script>
<style scoped>
  * {
    font: 13px/1.5 '微软雅黑';
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -box-sizing: border-box;
    padding: 0;
    margin: 0;
    list-style: none;
    box-sizing: border-box;
  }

  a {
    color: #27A9E3;
    text-decoration: none;
    cursor: pointer;
  }

  img {
    border: none;
  }


  .head {
    width: 100%;
    height: 80px;
    border-bottom: 1px solid #d6dfea;
    background-color: #eff4fa;
  }

  .tps_logo {
    float: left;
    width: 70px;
    height: 70px;

    margin-top: 5px;
    margin-left: 20px;
  }

  .tps_title {
    color: #388ecd;
    line-height: 55px;
    font-size: 40px;
    margin-left: 10px;
    margin-top: 10px;
  }

  .search{
    width: 26%;
    margin-left: 37%;
    margin-top: 80px;
  }
  table{
    margin-top: 100px;
    width:70%;
    margin-left: 15%;

  }
  .quit{
    float: right;
    margin-right: 20px;
    margin-top: 50px;
  }
</style>



