<template>
  <div id="app">
    <input v-model="ID" style="display: none">
    <component :is="currentView" :myMessage="ID" v-on:parent ="parentLisen" ></component>
  </div>
</template>

<script>
import Login from "./components/Login.vue";
import Test from "./components/test.vue";


export default {
  components: {
    Test,
    Login},
  name: 'App',
  data(){
    return{
      currentView:'Login',
      ID: ''
    };
  },
  mounted(){

  },
  methods:{
    handleChangeView:function(component){
      this.currentView=component;
    },
    parentLisen(evtValue) {
      //evtValue 是子组件传过来的值
      this.ID = evtValue.ID;
      this.currentView= evtValue.currentView;
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;

}
* {
  font: 13px/1.5 '微软雅黑';
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -box-sizing: border-box;
  padding: 0;
  margin: 0;
  list-style: none;
  box-sizing: border-box;
}

a {
  color: #27A9E3;
  text-decoration: none;
  cursor: pointer;
}

img {
  border: none;
}


</style>



